.UserInfo {
    border: 1px solid black;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.FormHolder {
    /* display: flex;
    align-items: center;
    justify-content: center; */
    width: 80%;
    /* border: 1px solid black; */
}
.Button{
    margin-top: 10px;
    border: 1px solid yellowgreen;
    display: flex;
    justify-content: center;
    margin: 10px 20%;
    height: 40px;
    border-radius: 20px;
    background-color: yellowgreen;
}

button{
    width: 100%;
    border: none;
    outline: none;
    border-radius: 20px;
    background: none;
}



@media(min-width: 499px) {
    .FormHolder {
        width: 400px;
    }
}
h4{
    margin: none;
    border: 1px solid black;
}

